const app = getApp()
import helper from "../componets/helper.js";
import MescrollMixin from "../componets/mescroll-uni/mescroll-mixins.js";
export default {
	mixins: [MescrollMixin], // 使用mixin
	data() {
		return {
			findList:[],
			imgUrl: '',
			downOption: {
				auto: false //是否在初始化后,自动执行downCallback; 默认true
			},
			upOption: {
				page: {
					num: 0, // 当前页码,默认0,回调之前会加1,即callback(page)会从1开始
					size: 10 // 每页数据的数量,默认10
				},
				noMoreSize: 1, // 配置列表的总数量要大于等于5条才显示'-- END --'的提示
				empty: {
					tip: '暂无相关数据'
				},
				textNoMore: '没有更多数据了'
			}
		}
	},
	onShow() {
		this.mescroll.resetUpScroll()
	},
	onLoad() {
		this.imgUrl = helper.imgUrl;
		// this.upCallback(page)
		// this.getList()
	},
	methods: {
		// 获取数据
		/*下拉刷新的回调 */
		downCallback() {
			this.mescroll.resetUpScroll()
		},
		 // 上拉回调 page = {num:1, size:10}; num:当前页 ,默认从1开始; size:每页数据条数,默认10
		upCallback (page) {
			this.getList(page)
		},
		getList(page) {
			console.log(page,'-----------------------')
			let pageNum = page.num; // 页码, 默认从1开始
			let pageSize = page.size; // 页长, 默认每页10条
			uni.showLoading({
				title: '加载中...',
			})
			uni.request({
				url: `${helper.requestUrl}discover/getDiscoverList`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				data: {
					page_size: pageSize,
					page: pageNum
				},
				success: res => {
					uni.hideLoading()
					res = helper.null2str(res);
					console.log(res);
					if (res.data.status_code == 'ok') {
						let curPageData = res.data.data.data
						let totalPage = res.data.data.total
						if(page.num == 1) this.findList = []; 
						this.findList = this.findList.concat(curPageData);
						console.log(this.findList)
						this.mescroll.endByPage(curPageData.length, totalPage); 
					}else {
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						});
					}
				}
			});
		},
		// 跳转详情
		getDetail(e){
			uni.navigateTo({
				url: `/pages/findDetail?id=${e}`
			})
		}
	}
}
